import { Header, Wrapper, StyledTable, TableHeader, TableCell, TableRow, Info, SubHeader, LinkWrapper, AddressLink, ContactWrapper, ContactInfo, ContactSection } from "../../styled";
import { ReactComponent as OneSideIcon } from "./priceIcons/1sideIcon.svg";
import { ReactComponent as TwoSideIcon } from "./priceIcons/2sidesIcon.svg";
import { ReactComponent as RoundIcon } from "./priceIcons/RoundIcon.svg";
import { ReactComponent as BikeIcon } from "./priceIcons/bicycle-54.svg";
import { ReactComponent as TrailerIcon } from "./priceIcons/bicycle-trailer-svgrepo-com.svg";

const PriceList = () => (
    <Wrapper id="cennik-rejsu">
        <Header>Cennik rejsw</Header>
        <StyledTable>
            <thead>
                <TableRow>
                    <TableHeader></TableHeader>
                    <TableHeader></TableHeader>
                    <TableHeader>Bilet normalny</TableHeader>
                    <TableHeader>Bilet ulgowy do lat 10</TableHeader>
                </TableRow>
            </thead>
            <tbody>
                <TableRow>
                    <TableCell data-label="Typ rejsu">Rejs w jedn stron</TableCell>
                    <TableCell data-label="Ikona"><OneSideIcon /></TableCell>
                    <TableCell data-label="Bilet normalny">17 PLN</TableCell>
                    <TableCell data-label="Bilet ulgowy">14 PLN</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ rejsu">Rejs w dwie strony</TableCell>
                    <TableCell data-label="Ikona"><TwoSideIcon /></TableCell>
                    <TableCell data-label="Bilet normalny">34 PLN</TableCell>
                    <TableCell data-label="Bilet ulgowy">28 PLN</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ rejsu">Rejs widokowy dookoa jeziora</TableCell>
                    <TableCell data-label="Ikona"><RoundIcon /></TableCell>
                    <TableCell data-label="Bilet normalny">35 PLN</TableCell>
                    <TableCell data-label="Bilet ulgowy">28 PLN</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ rejsu">Pasaer z rowerem</TableCell>
                    <TableCell data-label="Ikona"><BikeIcon /></TableCell>
                    <TableCell data-label="Bilet normalny">27 PLN</TableCell>
                    <TableCell data-label="Bilet ulgowy">Dzieci do lat 4 <strong>bezpatnie</strong></TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ rejsu">Przewz przyczepki rowerowej</TableCell>
                    <TableCell data-label="Ikona"><TrailerIcon /></TableCell>
                    <TableCell data-label="Bilet normalny">15 PLN</TableCell>
                    <TableCell data-label="Bilet ulgowy">15 PLN</TableCell>
                </TableRow>
            </tbody>
        </StyledTable>
        <Info>
            <strong>Dla grup zorganizowanych (szkolnych, firmowych, wycieczek)</strong> moliwo rezerwacji rejsu.<br />
            W przypadku grup zorganizowanych moliwo negocjacji ceny. <br />
            Wystawiamy faktury VAT. <br />
        </Info>
        <ContactWrapper>
            <ContactSection>
                <SubHeader>
                    Dane kontaktowe
                </SubHeader>
                <ContactInfo>
                    W celu rezerwacji rejsu prosimy o kontakt <br /> telefoniczny lub mailowy:
                </ContactInfo>
                <LinkWrapper>
                    <AddressLink href="tel:+48509438985" title="telefon">tel: 509 438 985</AddressLink>
                </LinkWrapper>
                <LinkWrapper>
                    <AddressLink href="mailto:biuro@zalewczorsztynski.com" title="biuro@zalewczorsztynski.com">biuro@zalewczorsztynski.com</AddressLink>
                </LinkWrapper>
            </ContactSection>
            <ContactSection>
                <SubHeader>Nasza lokalizaca w Czorsztynie</SubHeader>
                <ContactInfo>
                    Przysta Czorsztynianka <br />
                    ul. Wronina 1 <br />
                    34-440 Czorsztyn
                    <LinkWrapper>
                        <AddressLink href="https://maps.app.goo.gl/vbb4muGnBGXNeTh29" target="_blank" rel="noopener noreferrer" title="Link do Google Maps">Nawigacja z Google Maps</AddressLink>
                    </LinkWrapper>
                </ContactInfo>
            </ContactSection>
            <ContactSection>
                <SubHeader>Nasza lokalizaca w Niedzicy</SubHeader>
                <ContactInfo>
                    Pomost dla gondoli Niedzica <br />
                    Osiedle Zamek 11 <br />
                    34-441 Niedzica
                    <LinkWrapper>
                        <AddressLink href="https://maps.app.goo.gl/FuhZFBqRTVSLS9eT6" target="_blank" rel="noopener noreferrer" title="Link do Google Maps">Nawigacja z Google Maps</AddressLink>
                    </LinkWrapper>
                </ContactInfo>
            </ContactSection>
        </ContactWrapper>
    </Wrapper>
);

export default PriceList;
