import { Header, Wrapper, StyledTable, TableHeader, TableCell, TableRow, Info, SubHeader, LinkWrapper, AddressLink, ContactWrapper, ContactSection, ContactInfo } from "../../styled";
import { ReactComponent as BikeIcon } from "./bikesIcons/bicycle-54.svg";
import { ReactComponent as ElectricBikeIcon } from "./bikesIcons/bicycle-electric-svgrepo-com.svg";
import { ReactComponent as TrailerIcon } from "./bikesIcons/bicycle-trailer-svgrepo-com.svg";
import { ReactComponent as SeatIcon } from "./bikesIcons/car-seat-with-seatbelt-svgrepo-com.svg";
import { ReactComponent as HelmetIcon } from "./bikesIcons/bicycle-helmet-svgrepo-com.svg";

const BikesPriceList = () => (
    <Wrapper id="cennik-wypozyczalni">
        <Header>Cennik wypoyczalni rowerw</Header>
        <StyledTable>
            <thead>
                <TableRow>
                    <TableHeader></TableHeader>
                    <TableHeader></TableHeader>
                    <TableHeader>Do 3 godzin</TableHeader>
                    <TableHeader>Cay dzie</TableHeader>
                </TableRow>
            </thead>
            <tbody>
                <TableRow>
                    <TableCell data-label="Typ sprztu">Rower klasyczny</TableCell>
                    <TableCell data-label="Ikona"><BikeIcon /></TableCell>
                    <TableCell data-label="Do 3 godzin">50 PLN</TableCell>
                    <TableCell data-label="Cay dzie">60 PLN</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ sprztu">Rower elektryczny</TableCell>
                    <TableCell data-label="Ikona"><ElectricBikeIcon /></TableCell>
                    <TableCell data-label="Do 3 godzin">150 PLN</TableCell>
                    <TableCell data-label="Cay dzie">200 PLN</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ sprztu">Przyczepka</TableCell>
                    <TableCell data-label="Ikona"><TrailerIcon /></TableCell>
                    <TableCell data-label="Do 3 godzin">60 PLN</TableCell>
                    <TableCell data-label="Cay dzie">60 PLN</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ sprztu">Fotelik dziecicy</TableCell>
                    <TableCell data-label="Ikona"><SeatIcon /></TableCell>
                    <TableCell data-label="Do 3 godzin">30 PLN</TableCell>
                    <TableCell data-label="Cay dzie">30 PLN</TableCell>
                </TableRow>
                <TableRow>
                    <TableCell data-label="Typ sprztu">Kask</TableCell>
                    <TableCell data-label="Ikona"><HelmetIcon /></TableCell>
                    <TableCell data-label="Do 3 godzin">10 PLN</TableCell>
                    <TableCell data-label="Cay dzie">10 PLN</TableCell>
                </TableRow>
            </tbody>
        </StyledTable>
        <Info>* cay dzie jest rozumiany w godzinach otwarcia wypoyczalni <br />
            <strong>Klienci maj moliwo darmowego przewozu wypoyczonego roweru naszymi gondolami</strong>
        </Info>
        <ContactWrapper>
            <ContactSection>
                <SubHeader>
                    Dane kontaktowe
                </SubHeader>
                <ContactInfo>
                    W celu rezerwacji rowerw prosimy o kontakt telefoniczny lub mailowy:
                </ContactInfo>
                <LinkWrapper>
                    <AddressLink href="tel:+48570631403" title="telefon">tel: 570 631 403</AddressLink>
                </LinkWrapper>
                <LinkWrapper>
                    <AddressLink href="mailto:velo@zalewczorsztynski.com" title="velo@zalewczorsztynski.com">velo@zalewczorsztynski.com</AddressLink>
                </LinkWrapper>
            </ContactSection>
            <ContactSection>
                <SubHeader>Adres</SubHeader>
                <ContactInfo>
                    Wypoyczalnia rowerw "Velo Czorsztyn" <br />
                    Wronina 1 <br />
                    34-440 Czorsztyn
                    <LinkWrapper>
                        <AddressLink href="https://maps.app.goo.gl/eg1zaYx7ESMogBsj6" target="_blank" rel="noopener noreferrer" title="Link do Google Maps">Nawigacja z Google Maps</AddressLink>
                    </LinkWrapper>
                </ContactInfo>
            </ContactSection>
        </ContactWrapper>
    </Wrapper>
);

export default BikesPriceList;
